import { useState } from 'react'

function AddTodoForm({ globalFunctions }) {

    const [nameInput, setNameInput] = useState("");
    const [descriptionInput, setDescriptionInput] = useState("");
    const [startDateInput, setStartDateInput] = useState("");
    const [endDateInput, setEndDateInput] = useState("");
    const completedInput = false; // todo checked as completed
    const editModeInput = false; // todo checked to be edited
    const viewStateInput = true; // todo that has been cleared and cant be altered

    const onSubmit = (e) => {
        e.preventDefault();
        if (nameInput === "") return;

        const defaultDescriptionInput = (descriptionInput === "") ? 'No description given' : descriptionInput;
        const defaultStartDateInput = (startDateInput === "") ? new Date().toISOString().substring(0, 10) : startDateInput;
        const defaultEndDateInput = (endDateInput === "") ? new Date().toISOString().substring(0, 10) : endDateInput;
        setDescriptionInput(defaultDescriptionInput);
        setStartDateInput(defaultStartDateInput);
        setEndDateInput(defaultEndDateInput);

        globalFunctions.app.handleAddTodo(nameInput, defaultDescriptionInput, defaultStartDateInput, defaultEndDateInput, completedInput, editModeInput, viewStateInput);
        setNameInput("");
        setDescriptionInput("");
        setStartDateInput("");
        setEndDateInput("");
    }

    return (
        <form onSubmit={onSubmit} className="add-todo-form">
            <div className="controls-header">Add a new item</div>
            <div className="controls-wrapper">
                <div className="todo input">
                    <div className="todo-content">
                        <div>
                            <input type="text" placeholder="To do - name" value={nameInput} onChange={(e) => { setNameInput(e.target.value) }} />
                        </div>
                        <div>
                            <textarea type="textarea" placeholder="Description" value={descriptionInput} onChange={(e) => { setDescriptionInput(e.target.value) }} />
                        </div>
                        <div>
                            <div>
                                <label >From:</label>
                            </div>
                            <div>
                                <input id="start-date" type="date" value={startDateInput} onChange={(e) => { setStartDateInput(e.target.value) }} />
                            </div>
                        </div>
                        <div>
                            <div>
                                <label>To:</label>
                            </div>
                            <div>
                                <input id="end-date" type="date" value={endDateInput} onChange={(e) => { setEndDateInput(e.target.value) }} />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div>
                <input type="submit" value="Add todo" className="btn-type-1" />
            </div>
        </form>
    )

}

export default AddTodoForm;